/**
 * @module special-characters/specialcharactersmathematical
 */
import { Plugin } from 'ckeditor5/src/core.js';
/**
 * A plugin that provides special characters for the "Mathematical" category.
 *
 * ```ts
 * ClassicEditor
 *   .create( {
 *     plugins: [ ..., SpecialCharacters, SpecialCharactersMathematical ],
 *   } )
 *   .then( ... )
 *   .catch( ... );
 * ```
 */
export default class SpecialCharactersMathematical extends Plugin {
    /**
     * @inheritDoc
     */
    static get pluginName() {
        return 'SpecialCharactersMathematical';
    }
    /**
     * @inheritDoc
     */
    init() {
        const editor = this.editor;
        const t = editor.t;
        const plugin = editor.plugins.get('SpecialCharacters');
        plugin.addItems('Mathematical', [
            { character: '<', title: t('Less-than sign') },
            { character: '>', title: t('Greater-than sign') },
            { character: '‚â§', title: t('Less-than or equal to') },
            { character: '‚â•', title: t('Greater-than or equal to') },
            { character: '‚Äì', title: t('En dash') },
            { character: '‚Äî', title: t('Em dash') },
            { character: '¬Ø', title: t('Macron') },
            { character: '‚Äæ', title: t('Overline') },
            { character: '¬∞', title: t('Degree sign') },
            { character: '‚àí', title: t('Minus sign') },
            { character: '¬±', title: t('Plus-minus sign') },
            { character: '√∑', title: t('Division sign') },
            { character: '‚ÅÑ', title: t('Fraction slash') },
            { character: '√ó', title: t('Multiplication sign') },
            { character: '∆í', title: t('Latin small letter f with hook') },
            { character: '‚à´', title: t('Integral') },
            { character: '‚àë', title: t('N-ary summation') },
            { character: '‚àû', title: t('Infinity') },
            { character: '‚àö', title: t('Square root') },
            { character: '‚àº', title: t('Tilde operator') },
            { character: '‚âÖ', title: t('Approximately equal to') },
            { character: '‚âà', title: t('Almost equal to') },
            { character: '‚â†', title: t('Not equal to') },
            { character: '‚â°', title: t('Identical to') },
            { character: '‚àà', title: t('Element of') },
            { character: '‚àâ', title: t('Not an element of') },
            { character: '‚àã', title: t('Contains as member') },
            { character: '‚àè', title: t('N-ary product') },
            { character: '‚àß', title: t('Logical and') },
            { character: '‚à®', title: t('Logical or') },
            { character: '¬¨', title: t('Not sign') },
            { character: '‚à©', title: t('Intersection') },
            { character: '‚à™', title: t('Union') },
            { character: '‚àÇ', title: t('Partial differential') },
            { character: '‚àÄ', title: t('For all') },
            { character: '‚àÉ', title: t('There exists') },
            { character: '‚àÖ', title: t('Empty set') },
            { character: '‚àá', title: t('Nabla') },
            { character: '‚àó', title: t('Asterisk operator') },
            { character: '‚àù', title: t('Proportional to') },
            { character: '‚à†', title: t('Angle') },
            { character: '¬º', title: t('Vulgar fraction one quarter') },
            { character: '¬Ω', title: t('Vulgar fraction one half') },
            { character: '¬æ', title: t('Vulgar fraction three quarters') }
        ], { label: t('Mathematical') });
    }
}
