/**
 * @module special-characters/specialcharacterstext
 */
import { Plugin } from 'ckeditor5/src/core.js';
/**
 * A plugin that provides special characters for the "Text" category.
 *
 * ```ts
 * ClassicEditor
 *   .create( {
 *     plugins: [ ..., SpecialCharacters, SpecialCharactersText ],
 *   } )
 *   .then( ... )
 *   .catch( ... );
 * ```
 */
export default class SpecialCharactersText extends Plugin {
    /**
     * @inheritDoc
     */
    static get pluginName() {
        return 'SpecialCharactersText';
    }
    /**
     * @inheritDoc
     */
    init() {
        const editor = this.editor;
        const t = editor.t;
        const plugin = editor.plugins.get('SpecialCharacters');
        plugin.addItems('Text', [
            { character: '‚Äπ', title: t('Single left-pointing angle quotation mark') },
            { character: '‚Ä∫', title: t('Single right-pointing angle quotation mark') },
            { character: '¬´', title: t('Left-pointing double angle quotation mark') },
            { character: '¬ª', title: t('Right-pointing double angle quotation mark') },
            { character: '‚Äò', title: t('Left single quotation mark') },
            { character: '‚Äô', title: t('Right single quotation mark') },
            { character: '‚Äú', title: t('Left double quotation mark') },
            { character: '‚Äù', title: t('Right double quotation mark') },
            { character: '‚Äö', title: t('Single low-9 quotation mark') },
            { character: '‚Äû', title: t('Double low-9 quotation mark') },
            { character: '¬°', title: t('Inverted exclamation mark') },
            { character: '¬ø', title: t('Inverted question mark') },
            { character: '‚Ä•', title: t('Two dot leader') },
            { character: '‚Ä¶', title: t('Horizontal ellipsis') },
            { character: '‚Ä°', title: t('Double dagger') },
            { character: '‚Ä∞', title: t('Per mille sign') },
            { character: '‚Ä±', title: t('Per ten thousand sign') },
            { character: '‚Äº', title: t('Double exclamation mark') },
            { character: '‚Åà', title: t('Question exclamation mark') },
            { character: '‚Åâ', title: t('Exclamation question mark') },
            { character: '‚Åá', title: t('Double question mark') },
            { character: '¬©', title: t('Copyright sign') },
            { character: '¬Æ', title: t('Registered sign') },
            { character: '‚Ñ¢', title: t('Trade mark sign') },
            { character: '¬ß', title: t('Section sign') },
            { character: '¬∂', title: t('Paragraph sign') },
            { character: '‚Åã', title: t('Reversed paragraph sign') }
        ], { label: t('Text') });
    }
}
